import React from 'react'
import { Card, Container } from 'react-bootstrap'

const Index = () => {
  return (
    <Container fluid>
      <Card className="col-12 col-md-3 bg-dark text-white fs-5">
        <Card.Body className="text-center ">
          <p className="mb-3">Total Request for Approving Walet Withdrawal</p>
          <div className="p-3 text-center fs-2 bg-warning rounded">3</div>
        </Card.Body>
      </Card>
    </Container>
  )
}

export default Index
